import React from 'react';
import './App.css';
import * as Bounce from 'bounce.js'


class App extends React.Component{

  constructor(props){
    super(props)
    this.state = {
      play : false,
      audioUrl :'', 
      slapImg : '',
      slapCount : 0,
      enemies_name :'',
      enemies_img :'',
      songVideo:'',
      quoteStyle:'',

    }
  
  }


  componentDidMount(){
    this.setState({
      audioUrl : window.origin+"/tampolin/slap.wav",
      slapImg : window.origin+"/tampolin/slap.png",
      enemies_img :'https://www.w3schools.com/bootstrap4/img_avatar3.png',
      quoteStyle : 'none',
    })
    
  }
  

  enterName = (e)=>{
    this.setState({
      enemies_name : e.target.value
    })
  }
  enterLink = (e) =>{
    this.setState({
      enemies_img : e.target.value
    })
  }
  slap =()=> {
      this.setState({
        play : true
      })
      let bounce = new Bounce();
      let audio = new Audio(this.state.audioUrl)
      audio.play()
      bounce.translate({
        from: { x: -300, y: 50 },
        to: { x: 20, y: 0 }
      })
      .scale({
        from  : {x:1,y:15},
        to  : {x:1,y:1},
      })
      .applyTo(document.querySelectorAll(".animation-target"));
      let lastSlap = this.state.slapCount ;

      this.setState({
          slapCount : lastSlap+1
      })
      if(this.state.slapCount > 10){
        this.setState({
          songVideo:  "https://www.youtube.com/embed/XwjjbWg875Q?autoplay=1",
          quoteStyle : 'block'
        })
      }
  }

  render(){
    return(
      <div className="container-fluid">
         <div className="row">
            <div className="col-md-3">
                <h3 className="text-center slap-counter">
                    Slap Counter : 
                </h3>
                <h1 className="text-center">{this.state.slapCount}</h1>
            </div>
            <div className="col-md-4">
                <div className="animation-target">
                  <img src={this.state.slapImg}></img>
                </div>
                <div className="card">
                  <div className="card-body">
                     <img 
                      className="card-img-top" 
                      src={this.state.enemies_img}
                      alt="image"/>
                    
                      <p>{this.state.enemies_name}</p>
                      <input onKeyUp={(e)=>this.enterName(e)} 
                      placeholder="Insert your enemies name" 
                      className="form-control input-lg"></input>
                      <p>
                        <br></br>
                        <input onChange={(e)=>this.enterLink(e)} 
                        placeholder="Insert your enemies image link" 
                        className="form-control input-lg"
                        />
                      </p>
                  </div>
                  <div className="card-footer">
                  <button 
                    className="btn btn-block btn-danger btn-lg" 
                    onClick={this.slap}>
                      Tampols It Baby!!!
                  </button>
                </div>
                </div>
              
            </div>
            <div className="col-md-4 slap-counter">
              <iframe width="100%" height="300px" src={this.state.songVideo}
              frameBorder="0"
              allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture" 
              >
              </iframe>
              <p className="text-muted text-center" style={{display : this.state.quoteStyle}}>
              I offer you peace, I offer you love, I offer you friendship. I see your beauty, I hear your need, I feel your feelings. My wisdom flows from the highest source. I salute that Source in you. Let us work together for unity and peace 
              <span>- Mahatma Gandhi</span>
              </p>
             
              </div>
          </div>
          <div className="row text-center">
              <div className="col-md-12 navbar text-white fixed-bottom navbar-expand-sm navbar-dark bg-dark">
              Tampolin - 2019 by  :  <a href="https://www.youtube.com/agitnaeta">
                    @agitnaeta
              </a>
              </div>
          </div>
      </div>
    
    )
  }

}

export default App;
